@import url(https://fonts.googleapis.com/css?family=Rubik);
html,
body {
  height: 100%;
}

* {
  box-sizing: border-box;
}

body {
  background: #b22;
  margin: 0;
  padding: 0;
  font-family: -apple-system, 'Rubik', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  display: flex;
  justify-content: center;
}

.container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 800px;
  padding: 20px;
  margin: 20px;
  border: 2px solid linen;
  border-radius: 4px;
  background: -webkit-linear-gradient(top, snow, linen);
  background: linear-gradient(to bottom, snow, linen);
}

@media only screen and (min-width: 840px) {
  .container {
    padding: 20px 30px;
  }
}

.about {
  flex: 1 0 100%;
  text-align: center;
}

.headline {
  margin: 0;
  font-size: 50px;
  font-family: 'Pacifico', cursive;
  color: saddlebrown;
}

.subline {
  max-width: 500px;
  padding: 0 20px;
  margin: 1em auto;
  font-size: 15px;
  font-style: italic;
}

.beamerWrap {
  flex: 1 0 100%;
  margin: 30px auto 20px;
}

.beamer {
  width: 100%;
}

.cries {
  flex: 1 0 100%;
  margin-top: 10px;
}

.cry {
  font-size: 16px;
  line-height: 23px;
}

.cry:first-letter {
  text-transform: capitalize;
}

.credit {
  flex: 1 0 100%;
  margin: 30px auto 20px;
  font-size: 11px;
  text-align: center;
}

.credit a {
  text-decoration: none;
}
